import React from "react";

import { CrunchVariants } from "../../types/CrunchVariants";

import "./crunchesListItem.scss";

interface CrunchesListItemProps {
	value: CrunchVariants;
	selected: boolean;
	handleChange: () => void;
	imageSrc: string;
	title: string;
	groupName: string;
	disabled?: boolean;
}

const CrunchesListItem: React.FC<CrunchesListItemProps> = ({
	value,
	selected,
	handleChange,
	imageSrc,
	title,
	groupName,
	disabled = false,
}) => {
	return (
		<label className="crunch-item" data-checked={selected} data-disabled={disabled}>
			<input
				className="crunch-item__input visually-hidden"
				type="radio"
				value={value}
				checked={selected}
				onChange={handleChange}
				aria-checked={selected}
				aria-label={value === CrunchVariants.default ? "without a crunch" : value}
				name={groupName}
				disabled={disabled}
			/>
			<span className="crunch-item__image">
				<img src={imageSrc} alt={title} />
			</span>
			<span className="crunch-item__title">{title}</span>
		</label>
	);
};

export default CrunchesListItem;
