import React from "react";

import { CrunchVariants } from "../../types/CrunchVariants";
import CrunchesListItem from "../crunchesListItem/CrunchesListItem";

import "./crunchesList.scss";
import defaultImage from "../../assets/images/crunches-list/default.jpg";
import coconutImage from "../../assets/images/crunches-list/coconut.jpg";
import chocolateImage from "../../assets/images/crunches-list/chocolate.jpg";
import sesameImage from "../../assets/images/crunches-list/sesame.jpg";
import peanutImage from "../../assets/images/crunches-list/peanut.jpg";

interface CrunchesListProps {
	selectedValue: CrunchVariants;
	handleChange: (value: CrunchVariants) => void;
	name: string;
	disabled?: boolean;
}

const crunches = [
	{
		value: CrunchVariants.default,
		image: defaultImage,
		title: "Без кранчу",
	},
	{
		value: CrunchVariants.coconut,
		image: coconutImage,
		title: "Кокос",
	},
	{
		value: CrunchVariants.chocolate,
		image: chocolateImage,
		title: "Шоколад",
	},
	{
		value: CrunchVariants.sesame,
		image: sesameImage,
		title: "Кунжут",
	},
	{
		value: CrunchVariants.peanut,
		image: peanutImage,
		title: "Арахіс",
	},
];

const CrunchesList: React.FC<CrunchesListProps> = ({ selectedValue, handleChange, name, disabled = false }) => {
	return (
		<ul className="crunches-group" aria-label="available crunches">
			{crunches?.map(({ value, image, title }) => (
				<li className="crunches-group__item" key={value}>
					<CrunchesListItem
						value={value}
						imageSrc={image}
						title={title}
						groupName={name}
						selected={value === selectedValue}
						disabled={disabled}
						handleChange={() => handleChange(value)}
					/>
				</li>
			))}
		</ul>
	);
};

export default CrunchesList;
