import React from "react";

import Button from "../button/Button";

import "./productPageError.scss";
import imageWebp from "../../assets/images/product-page-error/error_img.webp";
import imageSrc from "../../assets/images/product-page-error/error_img.jpg";

const ProductPageError: React.FC = () => {
	return (
		<div className="product-page-error">
			<div className="container">
				<div className="product-page-error__inner">
					<picture className="product-page-error__image">
						<source srcSet={imageWebp} type="image/webp" />
						<img src={imageSrc} alt="Сталася помилка" />
					</picture>
					<p className="product-page-error__title">
						Сталася помилка при завантаженні інформації про продукт!
					</p>
					<p className="product-page-error__subtitle">
						На жаль, інформацію про продукт не було завантажено. Спробуйте, будь ласка, повторити Ваш запит
						пізніше!
					</p>
					<Button className="product-page-error__button" href="/" title="На головну" />
				</div>
			</div>
		</div>
	);
};

export default ProductPageError;
