import React from "react";
import { useParams } from "react-router-dom";
import { Helmet } from "react-helmet";

import { viewportWidthContext } from "../../context/viewportWidthContext";
import { useProductActions } from "../../hooks";
import { ButtonVariants, IProduct } from "../../types";
import { useGetProductQuery } from "../../services/mockApi";
import Button from "../../components/button/Button";
import SizesList from "../../components/sizesList/SizesList";
import CrunchesList from "../../components/crunchesList/CrunchesList";
import ProductUserActions from "../../components/productUserActions/ProductUserActions";
import ProductCardLabelsList from "../../components/productCardLabelsList/ProductCardLabelsList";
import CrunchesSelect from "../../components/crunchesSelect/CrunchesSelect";
import SectionProducts from "../../components/sectionProducts/SectionProducts";
import Loading from "../../components/loading/Loading";
import ProductPageError from "../../components/productPageError/ProductPageError";

import "./productPage.scss";

const ProductPage: React.FC = () => {
	const { isMobile } = React.useContext(viewportWidthContext);
	const { id } = useParams<IProduct["id"]>();
	const {
		doubleFilling,
		size,
		crunch,
		priceOld,
		priceCurrent,
		handleChangeDoubleFilling,
		handleChangeSize,
		handleChangeCrunch,
		resetLocalActionsToDefault,
	} = useProductActions(id as IProduct["id"]);
	const { data, isLoading, isFetching, isError } = useGetProductQuery({ id });

	// True when adding to cart animation is playing
	const [isAddingToCart, setAddingToCart] = React.useState<boolean>(false);

	React.useEffect(() => {
		resetLocalActionsToDefault();
	}, [id]);

	return (
		<>
			{(isLoading || isFetching) && <Loading />}
			{isError && <ProductPageError />}
			{data && !isFetching && !isError && (
				<>
					<Helmet>
						<title>{data.title} - Просто паста</title>
						<meta
							name="description"
							content="На сторінці можна замовити пасту. Також, обрати великий чи малий розмір. Можна додати додаткову начинку. Та і просто дізнатися ціну"
						/>
					</Helmet>
					<section className="page-product">
						<div className="container">
							<div className="page-product__top">
								<h1 className="page-product__title page-product__title--mobile">{data.title}</h1>
								<div className="page-product__media">
									<div className="page-product__labels">
										<ProductCardLabelsList labels={data.labels} />
									</div>
									<picture className="page-product__image">
										<source srcSet={data.image.webp} type="image/webp" />
										<img src={data.image.src} alt={data.title} />
									</picture>
								</div>
								<div className="page-product__content">
									<h1 className="page-product__title">{data.title}</h1>
									{data.doubleFilling && (
										<Button
											className="page-product__button"
											variant={
												doubleFilling
													? ButtonVariants.addedAdditionalFilling
													: ButtonVariants.removedAdditionalFilling
											}
											title={
												doubleFilling ? "Подвійна начинка додана!" : "Додати подвійну начинку"
											}
											onClick={handleChangeDoubleFilling}
											disabled={isAddingToCart}
										/>
									)}
									<div className="page-product__sizes">
										<SizesList
											selectedValue={size}
											handleChange={handleChangeSize}
											name="sizes-group-1"
											disabled={isAddingToCart}
										/>
									</div>
									<div className="page-product__crunches">
										{isMobile ? (
											<CrunchesSelect
												selectedValue={crunch}
												handleChange={handleChangeCrunch}
												disabled={isAddingToCart}
											/>
										) : (
											<CrunchesList
												selectedValue={crunch}
												handleChange={handleChangeCrunch}
												name="crunches-group-1"
												disabled={isAddingToCart}
											/>
										)}
									</div>
									<div className="page-product__actions">
										<div className="page-product__prices">
											{priceOld && (
												<data
													className="page-product__price page-product__price--old"
													value={`${priceOld} грн`}
													aria-label={`previous price ${priceOld} uah`}
												>
													{priceOld} грн
												</data>
											)}
											<data
												className="page-product__price page-product__price--current"
												value={`${priceCurrent} грн`}
												aria-label={`current price ${priceCurrent} uah`}
											>
												{priceCurrent} грн
											</data>
										</div>
										<div className="page-product__user-actions">
											<ProductUserActions
												productData={{
													id: data.id,
													title: data.title,
													image: data.image,
													price: priceCurrent,
												}}
												productOptions={{
													doubleFilling,
													size,
													crunch,
												}}
												isAddingToCart={isAddingToCart}
												setAddingToCart={setAddingToCart}
											/>
										</div>
									</div>
								</div>
							</div>
						</div>
						<div className="page-product__bottom">
							<div className="container">
								<div className="page-product__description">
									<div className="product-info">
										<h2 className="product-info__title">Про продукт</h2>
										<p className="product-info__nutritional-value">
											Поживна цінність 100г продукту ({data.nutritions.calories} ккал):
										</p>
										<p className="product-info__nutritional-value">
											Білки&nbsp;– <span>{data.nutritions.proteins}&nbsp;г</span>; Жири&nbsp;–{" "}
											<span>{data.nutritions.fats}&nbsp;г</span>; Вуглеводи&nbsp;–{" "}
											<span>{data.nutritions.carbohydrates}&nbsp;г</span>.
										</p>
										<p className="product-info__ingredients">Склад: {data.composition}</p>
										<p className="product-info__description">{data.description}</p>
									</div>
								</div>
							</div>
						</div>
						<div className="page-product__advertisement">
							<SectionProducts sectionTitle="Купують разом" exludedId={id} />
						</div>
					</section>
				</>
			)}
		</>
	);
};

export default ProductPage;
